<template>
    <div v-if="ready" style='height:85vh'>
        <div v-if="open.user" class="inner_item">
            <div style='position:relative'>
                <i class="fa fa-window-close" aria-hidden="true" @click="close_overlay('user')"></i>
                <v-data-table
                    :headers="headers"
                    :items="userlist"
                    :rows-per-page-items="[10]"
                    class="elevation-1"                
                    disable-initial-sort
                    show-select              
                    item-key="item_code"               
                >                     
                <template v-slot:items="props">                     
                    <tr style="cursor:pointer" @click="select_user(props.item.username,props.item.name)">
                        <td>{{ props.item.username }}</td>
                        <td>{{ props.item.name }}</td>
                    </tr>                
                    <template v-slot:pageText="props">
                        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                    </template>  
                </template>
                </v-data-table>
            </div>
        </div>
        <div v-if="open.item" class="inner_item">
            <div style='position:relative'>
                <i class="fa fa-window-close" aria-hidden="true" @click="close_overlay('item')"></i>
                <v-data-table
                    :headers="headers_item"
                    :items="item_data"
                    :rows-per-page-items="[10]"
                    class="elevation-1"                
                    disable-initial-sort
                    show-select              
                    item-key="item_code"               
                >          
                <template v-slot:items="props">                     
                    <tr style="cursor:pointer" @click="select_item(props.item)">
                        <td>{{ props.item.item_code }}</td>
                        <td>{{ props.item.item_eng_name }}</td>
                    </tr>                
                    <template v-slot:pageText="props">
                        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                    </template>  
                </template>
                </v-data-table>
            </div>
        </div>
        <form @submit.prevent="handleSubmit()">
            <div class="card col-12" style='height:100%'>
                <div class="card-header">
                    <div class="flex">
                        <strong>Insert New Borrow Record</strong>
                    </div>
                </div>            
                <div class="card-body card-block">
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Item Code
                        </div>
                        <div class="col-8">
                            <input type="text" class="form-control" placeholder="Enter Item Code" v-model="item_code" @keyup="search_item('item_code')" required/>
                        </div>                    
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Item Name
                        </div>
                        <div class="col-8">
                            <input type="text" class="form-control" placeholder="Item Name" readonly v-model="item_name" required/>
                        </div>                    
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Item Details
                        </div>
                        <div class="col-8">
                            <input type="text" class="form-control" placeholder="Serial Number" v-model="sn" @keyup="search_item('sn')" required/>
                        </div>                    
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Borrowed By
                        </div>
                        <div class="col-8">
                            <input type="text" class="form-control" placeholder="Search User" v-model="key" @keyup="search_user"/>
                        </div>                    
                    </div>
                    <div class="col-12 px0 inner_row">
                        <div class="col-4">
                            Borrowed Date
                        </div>
                        <div class="col-8">
                            <VueCtkDateTimePicker 
                                :noHeader="true"
                                :inline="true"
                                :noKeyboard="true"
                                :buttonColor="'rgb(237,28,37)'" 
                                :color="'rgb(237,28,37)'"                     
                                :format="'YYYY-MM-DD'" 
                                :id="'DatePicker'" 
                                :label="'Select Date'"
                                :noButtonNow="true"
                                :onlyDate="true"
                                v-model="borrow_date" 
                            >
                            </VueCtkDateTimePicker>
                        </div>                    
                    </div>               
                </div>
                <div class="card-footer">
                    <div class="flex">
                        <button type="submit" class="btn btn-primary btn-sm">
                            <i class="fa fa-paper-plane"></i> Submit
                        </button>                   
                        <button type="button" class="btn btn-danger btn-sm" @click="return_prev">
                            <i class="fa fa-undo"></i> Return
                        </button>
                    </div>
                </div>
            </div>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            headers: [
                { text: 'Username',value :'username',sortable: false},
                { text: 'Name',value :'name',sortable: true},
            ],
            headers_item: [
                { text: 'Item Code',value :'item_code',sortable: false},
                { text: 'Item Name',value :'item_eng_name',sortable: true},
            ],
            open:{
                user:false,
                item:false,
            },
            borrow_date:'',
            item_name:'',
            isSelected:false,
            ready:false,
            key:'',
            item_code:'',
            sn:'',
            type:'',
            borrow_by:'',
            borrow_id:'',
            userlist:[],
            item_data:[],
        }
    },
     watch:{
        item_code: function(newVal,oldVal){
            if(newVal==''){
                this.item_name = '';
                this.sn = '';
            }
        },
        sn: function(newVal,oldVal){
            if(newVal==''){
                this.item_name = '';
                this.item_code = '';
            }
        }
    },
    async created(){
        this.ready=true;
    },
    async mounted(){
        
    },
    methods:{
        clear(){
            this.item_name = '';
            this.item_code = '';
            this.sn = '';
        },
        close_overlay(mode){
            switch(mode){
                case 'user':
                    this.open.user=false;
                break;
                case 'item':
                    this.open.item=false;
                break;
            }
        },
        handleSubmit(){
            if(this.borrow_date!=''){
                this.$confirm('Confirm Submitting?')
                    .then(
                        ()=>{
                            var formData = new FormData();
                            formData.append('item_code',this.item_code);
                            formData.append('item_name',this.item_name);
                            formData.append('borrow_by',this.borrow_by);
                            formData.append('borrow_date',this.borrow_date);
                            axios.post('inventory/web/backend/submitBorrow.php',formData)
                                .then(
                                    (response)=>{
                                        if(response.data=="OK"){
                                            this.$alert("Successfully inserted item borrow record")
                                                .then(
                                                    ()=>{
                                                        this.$router.push('borrow');
                                                    }
                                                )
                                        } else {
                                            this.$alert(response.data);
                                        }
                                    }
                                )
                        }
                    )
            } else {
                this.$alert('Please Select Borrow Date');
            }
        },
        return_prev(){
            this.$router.push('borrow');
        },
        select_user(username,name){
            this.borrow_by = name;
            this.key = name;
            this.open.user=false;
        },
        search_user(){
            this.borrow_by = '';
            var formData = new FormData();
            formData.append('key',this.key);
            axios.post('inventory/app/backend/search_user.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.userlist = response.data;
                            this.open.user = true;
                        } else {
                            this.userlist = [];
                            this.open.user=false;
                        }
                    }
                )
        },
        select_item(item){
            this.sn = item.details;
            this.item_code = item.item_code;
            this.item_name = item.item_eng_name;
            this.isSelected = true;
            this.open.item = false;
        },
        search_item(tag){
            this.isSelected = false;
            var formData = new FormData();
            var key = '';
            if(tag=='sn'){
                key = this.sn;
                formData.append('key',this.sn);
            } else {
                key = this.item_code;
                formData.append('key',this.item_code);
            }
            if(key!=''){
                axios.post('inventory/web/backend/search_item_info.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                this.item_data = response.data;
                                this.open.item = true;
                            } else {
                                this.item_data = [];
                                this.open.item = false;
                            }
                        }
                    )
            } else {
                this.open.item=false;
            }
        }
    }
}
</script>
<style scoped>
    .px0{
        padding-left:0;
        padding-right:0;
    }
    .inner_row{
        display:flex;
        flex-direction:row;
        margin-bottom:1rem;
    }
    .fa-window-close{
        transition: all 0.15s ease-in-out;
        color:rgba(255, 255, 255, 0.55);
    }
    .fa-window-close:hover{
        color:rgba(255, 255, 255, 1);
    }
    .inner_item{
        position: absolute; 
        left: 0; 
        right: 0; 
        margin-top: 15vh;
        margin-left: auto; 
        margin-right: auto; 
        width: 50%;
        z-index: 100;
    }
    .fa-window-close{
        right:0;
        top:0;
        position:absolute;
        margin-left:auto;
        margin-top:0.25rem;
        margin-right:0.5rem;
        cursor:pointer;
        z-index: 101;
        transition: all 0.15s ease-in-out;
        color:rgba(255, 255, 255, 0.55);
    }
    .fa-window-close:hover{
        color:rgba(255, 255, 255, 1);
    }
</style>


